

.wrapper{
	display: grid;
	grid-template-rows: 100px 100px auto 100px 100px;
	grid-template-areas: "header header header"
						 "nav nav nav"
						 "main main main"
						 "article article article"
						 "footer footer footer";
	min-height:100vh;
}

.header{
	grid-area:header;
	background:#A9A9A9 ;
}
.nav{
	grid-area:nav;
	background-color:#C0C0C0 ;
}
.main{
	grid-area:main;
	background:lightgrey;
}
.article{
	grid-area:article;
	background:#A9A9A9;
}

.footer{
	grid-area:footer;
	background:black;
}
