     @media screen and (max-width: 701px){
  .menu_box {
      position: fixed;
      width: 70px;
      left: -112px;
      top: 0;
     /* background: #8B0000;*/
      background-color: #71777E;
      height: auto;
      margin:8px 2px 2px 0px;
      z-index: 1;
} 
.menu_btn {
      position: fixed;
      left: 10;
      top:8px;
      color: black;
      cursor: pointer;
      width: 20px;
      z-index: 2;
}
#menu_check {
      display: none;
}

#menu_check:checked  ~ .menu_box {
      left: 0;
}
#menu_check:checked  ~ .menu_btn {
      left: 0px;
}
.menu_box{
  display: flex;
  flex-direction: column;
}
 .span{
  margin-bottom: 10px;
  width: 200px;
  margin-left: 120px;
  padding-bottom: 10px;
 }
 .active{
    color: red;
    text-decoration: underline;
    font-size: 14px;
    border:1px solid #71777E;
    background-color: white;
    color: black;
  }
  .fa-bars:hover{
    color: red;
    font-size: 30px;
  }
  .fa-times-circle:hover{
    color: red;
    font-size: 30px;
  }
}
      