.article{
	/*background-image: url(page-wrapper-bg.png);*/
	width: 98vw;
	margin-top: -100px;
	margin-bottom: -22px;
	height: 60vh;
	background: linear-gradient(to left, white, #28313C);
}
@keyframes anim {
from {transform: rotateY(0deg);}
to {transform: rotateY(360deg);}
}

.wrapper {

position: relative;
width: 60%;
height: 300px;
margin: 100px auto;

animation: anim 16s linear infinite;
transform-style: preserve-3d;
}

.wrapper > div {
margin-top: 20px;
position: absolute;
border: 1px solid black;
width: 200px;
height: 300px;
line-height: 300px;
text-align: center;
color: #3B0B0B;
/*text-shadow: 3px 3px black;*/
font-size: 27px;
transform-style: preserve-3d;
}

.wrapper > div > div {
width: 100%;
height: 100%;
}

@keyframes anim2 {
0%, 100% {transform: rotateY(180deg);}
50% {transform: rotateY(0deg);}
}

.block1 {
transform: translateZ(300px);
background-image: url('https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcR_ihz4XNgrapRLXDeAXKa6uFVs7_OSq3k7lg&usqp=CAU');
}

.block2 {
transform: translateZ(-300px) rotateY(0deg);
background-image: url('https://s1.1zoom.ru/b4558/858/Money_Banknotes_Roubles_Texture_5000_568146_1080x1920.jpg');
background-size: 300px 300px;
background-repeat: no-repeat;
}

.block3 {
transform: translateX(300px) rotateY(-90deg);
background-image: url('https://img4.goodfon.ru/wallpaper/nbig/d/25/euro-euro-cent-valiuta-dengi.jpg');
background-size: 300px 300px;
}

.block4 {
transform: translateX(-300px) rotateY(-90deg);
background-image: url('data:image/jpeg;base64,' );
background-size: 300px 300px;
background-repeat: no-repeat;
}

.block1 div {
animation: anim2 16s infinite step-end 4s;
}

.block2 div {
animation: anim2 16s infinite step-end 4s;
}

.block3 div {
animation: anim2 16s infinite step-end 8s;
}

.block4 div {
animation: anim2 16s infinite step-end 8s;
}
.currency{
	color: #5C3128;
	text-shadow: 2px 2px #5C3128;
}